@import 'colors';

:local(.store) {
    margin: 10% 2% 2%;

    header {
        background-color: $header;
        margin: 0;
        position: fixed;
        width: 100%;
        top: 0;
        left: 0;
        z-index: 1000;

        div {
            float: right;
            margin: 0 2% 0 0;
        }

        h1 {
            font-size: 2.5em;
            text-align: left;
            width: 50%;
            margin: 1% 0 1% 5%;
            display: inline-block;
        }

    }

}



